export default function App() {

    return (
      <Router>
        <Route path="/" exact render={(props) => <Dashboard {...props} />} />
        <Route path="/Dashboard" render={(props) => <Dashboard {...props} />} />
        <Route path="/PieCharts" render={(props) => <PieCharts {...props} />} />
        <Route path="/BarCharts" render={(props) => <BarCharts {...props} />} />
        <Route path="/TableCharts" render={(props) => <TableCharts {...props} />} />
        <Route path="/LineCharts" render={(props) => <LineCharts {...props} />} />
        <Route path="/RaceCharts" render={(props) => <RaceCharts {...props} />} />
        <Route path="/DonutCharts" render={(props) => <DonutCharts {...props} />} />
        <Route path="/MultiLineCharts" render={(props) => <MultiLineCharts {...props} />} />
      </Router>
    )
}