import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import LastPageIcon from '@material-ui/icons/LastPage';

const useStyles1 = makeStyles((theme) => ({
  root: {
    flexShrink: 0,
    marginLeft: theme.spacing(2.5),
  },
}));

function TablePaginationActions(props) {
  const classes = useStyles1();
  const theme = useTheme();
  const { count, page, rowsPerPage, onChangePage } = props;

  const handleFirstPageButtonClick = (event) => {
    onChangePage(event, 0);
  };

  const handleBackButtonClick = (event) => {
    onChangePage(event, page - 1);
  };

  const handleNextButtonClick = (event) => {
    onChangePage(event, page + 1);
  };

  const handleLastPageButtonClick = (event) => {
    onChangePage(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
  };

  return (
    <div className={classes.root}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
      </IconButton>
      <IconButton onClick={handleBackButtonClick} disabled={page === 0} aria-label="previous page">
        {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="last page"
      >
        {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
      </IconButton>
    </div>
  );
}

TablePaginationActions.propTypes = {
  count: PropTypes.number.isRequired,
  onChangePage: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  rowsPerPage: PropTypes.number.isRequired,
};

const useStyles2 = makeStyles({
  table: {
    minWidth: 500,
  },
});

export default function TableChart(props) {
  const classes = useStyles2();
  const pdata = props.covid19;
  let data = [];
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(30);

  if (pdata.length > 0) {
    const confirmed = pdata[0];
    const deaths = pdata[1];
    const recovered = pdata[2];
    const loc = pdata[3];

    data = confirmed.map((item, i) => {
      const foundlocation = loc.find((itmInner) => itmInner['Province/State'] === confirmed[i]['Province/State'] && itmInner['Country/Region'] === confirmed[i]['Country/Region']);
      const founddeaths = deaths.find((itmInner) => itmInner['Province/State'] === confirmed[i]['Province/State'] && itmInner['Country/Region'] === confirmed[i]['Country/Region']);
      const foundrecovered = recovered.find((itmInner) => itmInner['Province/State'] === confirmed[i]['Province/State'] && itmInner['Country/Region'] === confirmed[i]['Country/Region']);
      const recover = foundrecovered ? foundrecovered['Value'] : 0
      return Object.assign({}, item,
        { Region: foundlocation['Region Name'] },
        { Subregion: foundlocation['Sub-region Name'] },
        { Deaths: founddeaths['Value'] },
        { Recovered: recover });
    })

    data.sort((a, b) => a['Region Code'] - b['Region Code']);
  }

  const emptyRows = rowsPerPage - Math.min(rowsPerPage, (data.length > 0 ? data.length : 0) - page * rowsPerPage);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };


  return (
    data.length > 0 ? (
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="custom pagination table">
          <TableBody>
            <TableRow style={{ backgroundColor: "#3F51B5" }} key={"Header"}>
              <TableCell style={{ color: "white" }}>Region</TableCell>
              <TableCell style={{ color: "white" }} component="th" >Countries</TableCell>
              <TableCell style={{ color: "white" }}>Province/State</TableCell>
              <TableCell style={{ color: "white" }}>Confirmed</TableCell>
              <TableCell style={{ color: "white" }}>Deaths</TableCell>
              <TableCell style={{ color: "white" }}>Rate</TableCell>              
              <TableCell style={{ color: "white" }}>Recovered</TableCell>
              <TableCell style={{ color: "white" }}>Rate</TableCell>
            </TableRow>
            {(rowsPerPage > 0
              ? data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              : data
            ).map((row) => (
              <TableRow key={row.Region + row.Subregion + row['Country/Region'] + row['Province/State']}>
                <TableCell component="th">{row.Region}</TableCell>
                <TableCell component="th">{row['Country/Region']}</TableCell>
                <TableCell component="th">{row['Province/State']}</TableCell>
                <TableCell align="right">{row.Value.toLocaleString()}</TableCell>
                <TableCell align="right">{row.Deaths.toLocaleString()}</TableCell>
                <TableCell align="right">{((row.Deaths / row.Value) * 100).toFixed(2).toLocaleString()}</TableCell>                
                <TableCell align="right">{row.Recovered.toLocaleString()}</TableCell>
                <TableCell align="right">{((row.Recovered / row.Value) * 100).toFixed(2).toLocaleString()}</TableCell>
              </TableRow>
            ))}

            {emptyRows > 0 && (
              <TableRow style={{ height: 60 * emptyRows }}>
                <TableCell colSpan={6} />
              </TableRow>
            )}
          </TableBody>
          <TableFooter>
            <TableRow>
              <TablePagination
                rowsPerPageOptions={[30, 60, 90, 150, { label: 'All', value: -1 }]}
                colSpan={9}
                count={data.length}
                rowsPerPage={rowsPerPage}
                page={page}
                SelectProps={{
                  inputProps: { 'aria-label': 'rows per page' },
                  native: true,
                }}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
                ActionsComponent={TablePaginationActions}
              />
            </TableRow>
          </TableFooter>
        </Table>
      </TableContainer>
    ) : null
  );
}