import React, { Component } from 'react';
import * as d3 from 'd3';
import * as topojson from 'topojson';
import legend from './d3color-legend';


class WorldChoropleth extends Component {

    constructor() {
        super();
        this.drawChoropleth = this.drawChoropleth.bind(this);
        this.state = {
            covid: []
        };
    }

    componentDidMount() {
        this.drawChoropleth();
    }

    drawChoropleth() {
        const node = this.node;
        const pdata = this.props.covid19;
        const width = window.innerWidth;
        const height = window.innerHeight;
        const margin = { top: 55, right: 200, bottom: 55, left: 200 };        
        //const outline = ({ type: "Sphere" })
        const projection = d3.geoEqualEarth();
        const path = d3.geoPath(projection);

        if (pdata.length > 0) {
            const confirmed = pdata[0];
            const world = pdata[3];
            let total = 0;

            const data = confirmed.reduce((r, o) => {
                const key = o['Country/Region'];
                r.get(key) ? total += o.Value : total = o.Value;
                return r.set(key, total);
            }, new Map())

            const countries = topojson.feature(world, (world.objects.countries));

            const color = d3.scaleThreshold()
                .domain([100, 500, 1000, 5000, 10000, 50000, 100000, 500000, 1000000])
                .range(['rgb(222,235,247)', 'rgb(198,219,239)', 'rgb(158,202,225)',
                    'rgb(107,174,214)', 'rgb(88,164,209)', 'rgb(66,146,198)', 'rgb(33,113,181)',
                    'rgb(8,81,156)', 'rgb(8,48,107)']);

            legend({
                node: node,
                xcor: -10,
                ycor: -475,
                color: d3.scaleThreshold([100, 500, 1000, 5000, 10000, 50000, 100000, 500000, 1000000],
                    ['rgb(222,235,247)', 'rgb(198,219,239)', 'rgb(158,202,225)',
                        'rgb(107,174,214)', 'rgb(88,164,209)', 'rgb(66,146,198)', 'rgb(33,113,181)',
                        'rgb(8,81,156)', 'rgb(8,48,107)']),
                title: "Total",
                tickSize: 0,
                width: 450
            });

            const svg = d3.select(node)
                .style("display", "block")
                .attr("viewBox", [0, 0, width - margin.left - margin.right, height - margin.top - margin.bottom]);

            const g = svg.append("g")
                .attr("clip-path", `url(${new URL("#clip", window.location)})`);

            // g.append("use")
            //     .attr("xlink:href", new URL("#outline", window.location))
            //     .attr("fill", "white");

            g.append("g")
                .selectAll("path")
                .data(countries.features)
                .join("path")
                .attr("fill", (d) => color(data.get(d.properties.name)))
                .attr("d", path)
                .append("title")
                .text(d =>
                    `Country: ${d.properties.name} \nTotal: ${data.has(d.properties.name) ? data.get(d.properties.name).toLocaleString() : "N/A"}`
                );

            g.append("path")
                .datum(topojson.mesh(world, world.objects.countries, (a, b) => a !== b))
                .attr("fill", "none")
                .attr("stroke", "white")
                .attr("stroke-linejoin", "round")
                .attr("d", path);

           svg.append("text")
                .attr("class", "caption")
                .attr("x", width - margin.left - margin.right - 200)
                .attr("y", height - margin.top - margin.bottom - 25)
                .attr("font-size", 12)
                .text("*Source: data.humdata.org");

            // svg.append("use")
            //     .attr("xlink:href", new URL("#outline", window.location))
            //     .attr("fill", "none")
            //     .attr("stroke", "black");
        }
    }

    render() {
        this.drawChoropleth()
        return <svg ref={node => { this.node = node }} />
    }
}

export default WorldChoropleth