import React, { Component } from 'react';
import { withStyles } from "@material-ui/core/styles";
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';


const useStyles = theme => ({
    root: {
        minWidth: 275,
        padding: 5,
    },
    title: {
        fontSize: 12,
    },
    container: {
        paddingLeft: theme.spacing(2),
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        paddingRight: theme.spacing(2),
    },
});

class GoogleNewsFeed extends Component {

    constructor() {
        super()
        this.state = {
            data: null,
        }
    }

    async componentDidMount() {
        const proxyurl = "https://cors-anywhere.herokuapp.com/";
        const url = "https://news.google.com/rss/search?q=covid19";
        const parser = new DOMParser();

        const newsfeed = await fetch(proxyurl + url);
        const newsfeedbody = await newsfeed.text();
        const newsfeeddom = parser.parseFromString(newsfeedbody, "text/xml");
        const newsfeeditem = newsfeeddom.getElementsByTagName("item");
        const layout = Array.from(newsfeeditem);
        this.setState({ data: layout })
    }

    render() {
        const { classes } = this.props;

        return (
            this.state.data ? (
                (this.state.data.slice(0, 10)).map((row) => (
                    <Grid key={row.getElementsByTagName("guid")[0].textContent} container spacing={2}>
                        <Grid item xs={6} md={10} lg={12}>
                            <Card className={classes.root}>
                                <CardContent className={classes.root}>
                                    <Typography className={classes.title} gutterBottom> {row.getElementsByTagName("pubDate")[0].textContent} </Typography>
                                    <Typography color="primary" variant="h6"> {row.getElementsByTagName("source")[0].textContent} </Typography>
                                    <Typography variant="body2"> {row.getElementsByTagName("title")[0].textContent} </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button color="primary" size="small" onClick={()=> window.open(row.getElementsByTagName("link")[0].textContent)}>More</Button>
                                </CardActions>
                            </Card>
                        </Grid>
                    </Grid>
                ))
            ) : null
        )
    }
}

export default withStyles(useStyles)(GoogleNewsFeed);