import React, { useState, useEffect, useRef } from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Toolbar from '@material-ui/core/Toolbar';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';

import * as d3 from 'd3';
import TwitterContainer from './TwitterContainer';

import Navigation from './Navigation';
import Copyright from '../Copyright';
import BarChart from './BarChart';


const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    title: {
        flexGrow: 1,
    },
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
    },
    container: {
        paddingLeft: theme.spacing(2),
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        paddingRight: theme.spacing(2),
    },
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 450,
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 115,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));


export default function BarCharts(props) {
    const classes = useStyles();
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

    const [covid19, setCovid19] = useState([])

    const covid19data = (props) => {
        const covid19confirmed = "time_series_covid19_confirmed_global_narrow.csv"
        const covid19deaths = "time_series_covid19_deaths_global_narrow.csv"
        const covid19recovered = "time_series_covid19_recovered_global_narrow.csv"
        const parsedate = d3.timeFormat("%Y-%m-%d")
        const data = Promise.all([
            d3.csv(covid19confirmed, d3.autoType).then(data =>
                data.filter(d => parsedate(d.Date) === props.yesterday)),
            d3.csv(covid19deaths, d3.autoType).then(data =>
                data.filter(d => parsedate(d.Date) === props.yesterday)),
            d3.csv(covid19recovered, d3.autoType).then(data =>
                data.filter(d => parsedate(d.Date) === props.yesterday)),
        ]).then(result => {
            setCovid19([result[0], result[1], result[2]]);
        });
        return data
    }

    useEffect(() => {
        const parsedate = d3.timeFormat("%Y-%m-%d")
        let yesterday = new Date();
        yesterday = parsedate(yesterday.setDate(yesterday.getDate() - 1));
        covid19data({ yesterday });
    }, []);

    const usePreviousValue = value => {
        const ref = useRef();
        useEffect(() => {
            ref.current = value;
        });
        return ref.current;
    };

    const valueby = ["Confirmed", "Deaths", "Recovered"];
    const [value, setValue] = useState("");
    const prevValue = usePreviousValue(value);

    const twitterdata = [
        // { id: 0, source: "https://twitter.com/WHO", title: "World Health Organization" },
        { id: 1, source: "https://twitter.com/ChannelNewsAsia", title: "Channel News Asia" },
        { id: 2, source: "https://twitter.com/bbcworld", title: "BBC World" },
        { id: 3, source: "https://twitter.com/CNN", title: "CNN" },
        { id: 4, source: "https://twitter.com/EuroNews", title: "Euro News" },
    ];


    return (
        <div className={classes.root}>
            <TwitterContainer />
            <Navigation />
            <main className={classes.content}>
                <Toolbar />
                <div className={classes.appBarSpacer} />
                <Container maxWidth="lg" className={classes.container}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={6} lg={9}>
                            <Grid item xs={12} md={8} lg={12}>
                                <FormControl className={classes.formControl}>
                                    <InputLabel id="bar-select-label">Top 50 Cases</InputLabel>
                                    <Select
                                        labelId="bar-select-label"
                                        id="bar-select"
                                        value={value}
                                        onChange={e => setValue(e.target.value)}
                                    >
                                        {valueby.map((row) => {
                                            return (<MenuItem key={row} value={row}>{row}</MenuItem>)
                                        })}
                                    </Select>
                                </FormControl>
                                <FormHelperText>*Source: data.humdata.org</FormHelperText>
                            </Grid>
                            <Grid item xs={12} md={8} lg={12}>
                                <Paper className={fixedHeightPaper}>
                                    <BarChart covid19={covid19} value={value} prevValue={prevValue} />
                                </Paper>
                            </Grid>
                        </Grid>
                        <Grid item xs={12} md={3} lg={3}>
                            <Paper className={fixedHeightPaper}>
                                <a className="twitter-timeline" href="https://twitter.com/WHO">WHO</a>
                            </Paper>
                        </Grid>
                    </Grid>
                    <Grid container spacing={2}>
                        {twitterdata.map((row) => {
                            return (
                                <Grid item key={row.id} xs={12} md={3} lg={3}>
                                    <Paper className={fixedHeightPaper}>
                                        <a className="twitter-timeline" href={row.source}>{row.title}</a>
                                    </Paper>
                                </Grid>)
                        })}
                    </Grid>
                    <Box pt={4}>
                        <Copyright />
                    </Box>
                </Container>
            </main>
        </div>
    )
}