import React, { useRef } from 'react';
import * as d3 from 'd3';


export default function BarChart(props) {

    const ref = useRef();
    const pdata = props.covid19;
    const pvalue = props.value;
    const margin = ({ top: 20, right: 20, bottom: 150, left: 70 });
    const width = window.innerWidth;
    const height = window.innerHeight;
    const color = "steelblue";

    if (pdata.length > 0) {
        let temp = [];

        switch (pvalue) {
            case "Confirmed": temp = pdata[0];
                break;
            case "Deaths": temp = pdata[1];
                break;
            case "Recovered": temp = pdata[2];
                break;
            default: temp = pdata[0];
        }

        const data = [...temp.reduce((r, o) => {
            const key = o['Country/Region'];
            const item = r.get(key) || Object.assign({}, { country: o['Country/Region'] }, { total: 0 });
            item.total += o.Value;
            return r.set(key, item);
        }, new Map()).values()].sort((a, b) => b.total - a.total)
            .slice(0, 50)

        const svg = d3.select(ref.current)
            .attr("viewBox", [0, 0, width, height]);

        const x = d3.scaleBand()
            .domain(d3.range(data.length))
            .range([margin.left, width - margin.right])
            .padding(0.1);

        const y = d3.scaleLinear()
            .domain([0, d3.max(data, d => d.total)]).nice()
            .range([height - margin.bottom, margin.top]);

        const xAxis = d3.axisBottom()
            .scale(x)
            .tickFormat(i => data[i].country);

        const yAxis = d3.axisLeft()
            .scale(y)
            .ticks(15, data.format);

        svg.append("text")
            .attr("x", width / 2)
            .attr("y", height)
            .attr("fill", "currentColor")
            .attr("text-anchor", "middle")
            .attr("font-weight", "bold")
            .attr('font-size', '20px')
            .text("Country");

        svg.append("text")
            .attr("transform", "rotate(-90)")
            .attr("x", -height / 2)
            .attr("y", -margin.left)
            .attr("dy", "1em")
            .attr("fill", "currentColor")
            .attr("text-anchor", "middle")
            .attr("font-weight", "bold")
            .attr('font-size', '20px')
            .text("Total");

        if (pvalue === "") {

            svg.append('g')
                .attr('class', 'x axis')
                .attr("transform", `translate(0,${height - margin.bottom})`)
                .call(xAxis)
                .attr('font-size', '16px')
                .selectAll("text")
                .attr("transform", "rotate(-90)")
                .attr("text-anchor", "end")
                .attr("dx", "-.5em")
                .attr("dy", "-.5em");

            svg.append('g')
                .attr('class', 'y axis')
                .attr("transform", `translate(${margin.left},0)`)
                .call(yAxis)
                .attr('font-size', '16px');

            svg.append("g")
                .attr('class', '.rect')
                .attr("fill", color)
                .selectAll("rect")
                .data(data)
                .join("rect")
                .attr("x", (d, i) => x(i))
                .attr("y", d => y(d.total))
                .attr("height", d => y(0) - y(d.total))
                .attr("width", x.bandwidth());

        } else {

            const trans = svg.transition().duration(300);
            const trans1 = trans.transition();

            svg.selectAll(".rect")
                .exit()
                .transition()
                .delay(100)
                .duration(300)
                .remove();

            svg.selectAll("rect")
                .data(data)
                .transition()
                .duration(1000)
                .ease(d3.easeBounceOut)
                .attr("x", (d, i) => x(i))
                .attr("y", d => y(d.total))
                .attr("height", d => y(0) - y(d.total))
                .attr("width", x.bandwidth());

            trans1.selectAll(".x.axis").call(xAxis);
            trans1.selectAll(".y.axis").call(yAxis);
        }
    }

    return <svg ref={ref} />
}