import React, { useEffect } from "react";

const FlipboardContainer = () => {

    useEffect(() => {
        const script = document.createElement("script");
        script.src = "https://cdn.flipboard.com/web/buttons/js/flbuttons.min.js";
        document.getElementsByClassName("flipboard-embed")[0].appendChild(script);
    }, []);

    return (
        <section className="flipboardContainer">
            <div className="flipboard-embed">
                {/* <a 
                    data-flip-widget="mag"
                    href="https://flipboard.com/@time/coronavirus-k68errrbz"
                    >
                    </a> */}
            </div>
        </section>
    );
};

export default FlipboardContainer;