import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Toolbar from '@material-ui/core/Toolbar';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';
import * as d3 from 'd3';

import Navigation from './Navigation';
import Copyright from '../Copyright';
import LineChart from './LineChart';


const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    title: {
        flexGrow: 1,
    },
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
    },
    container: {
        paddingLeft: theme.spacing(2),
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        paddingRight: theme.spacing(2),
    },
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 400,
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));


export default function LineCharts(props) {
    const classes = useStyles();
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

    const [covid19, setCovid19] = useState([])

    const covid19data = (props) => {
        const covid19confirmed = "time_series_covid19_confirmed_global_narrow.csv"
        const covid19deaths = "time_series_covid19_deaths_global_narrow.csv"
        const covid19recovered = "time_series_covid19_recovered_global_narrow.csv"
        const data = Promise.all([
            d3.csv(covid19confirmed, d3.autoType).then(data =>
                data.filter(d => d['Country/Region'] !== "#country+name")),
            d3.csv(covid19deaths, d3.autoType).then(data =>
                data.filter(d => d['Country/Region'] !== "#country+name")),
            d3.csv(covid19recovered, d3.autoType).then(data =>
                data.filter(d => d['Country/Region'] !== "#country+name")),
        ]).then(result => {
            setCovid19([result[0], result[1], result[2]]);
        });
        return data
    }

    useEffect(() => {
        const parsedate = d3.timeFormat("%Y-%m-%d")
        let yesterday = new Date();
        yesterday = parsedate(yesterday.setDate(yesterday.getDate() - 1));
        covid19data({ yesterday });
    }, []);

    let countries = [];
    if (covid19.length > 0) {
        countries = [...new Set(covid19[0].map(itm => itm['Country/Region']))];
    }

    const [value, setValue] = useState("");

    const handleChange = (event) => {
        setValue(event.target.value);
    };

    const youtubedata = [
        { id: 0, source: "https://www.youtube.com/embed/9Auq9mYxFEE", title: "CNA News" },
        { id: 1, source: "https://www.youtube.com/embed/w_Ma8oQLmSM", title: "ABC News" },
        { id: 2, source: "https://www.youtube.com/embed/9Auq9mYxFEE", title: "Sky News" },
        { id: 3, source: "https://www.youtube.com/embed/tPeUHECNLKs", title: "NBC News" },
        // { id: 4, source: "https://www.youtube.com/embed/BJzSJlMwQcA", title: "Euro News" },
    ];


    return (
        <div className={classes.root}>
            <Navigation />
            <main className={classes.content}>
                <Toolbar />
                <div className={classes.appBarSpacer} />
                <Container maxWidth="lg" className={classes.container}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={6} lg={9}>
                            <Grid item xs={12} md={8} lg={12}>
                                <FormControl className={classes.formControl}>
                                    <InputLabel id="countries-select-label">Countries</InputLabel>
                                    <Select
                                        labelId="countries-select-label"
                                        id="countries-select"
                                        value={value}
                                        onChange={handleChange}
                                    >
                                        {countries.map((row) => (
                                            <MenuItem key={row} value={row}>{row}</MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                                <FormHelperText>*Source: data.humdata.org</FormHelperText>
                            </Grid>
                            <Grid item xs={12} md={8} lg={12}>
                                <Paper className={fixedHeightPaper}>
                                    <LineChart covid19={covid19} value={value} />
                                </Paper>
                            </Grid>
                        </Grid>
                        <Grid item xs={12} md={4} lg={3}>
                            {youtubedata.map((row) => {
                                return (<Grid item key={row.id} xs={12} md={4} lg={12}>
                                    <Typography variant="h6" component="h2">{row.title}</Typography>
                                    <Paper>
                                        <div
                                            className="video"
                                            style={{
                                                position: "relative",
                                                paddingBottom: "56.25%",
                                                paddingTop: 10,
                                                height: 0
                                            }}
                                        >
                                            <iframe
                                                style={{
                                                    position: "absolute",
                                                    width: "100%",
                                                    height: "90%"
                                                }}
                                                src={row.source}
                                                frameBorder='0'
                                                allow='autoplay; encrypted-media'
                                                allowFullScreen
                                                title={row.title}
                                            />
                                        </div>
                                    </Paper>
                                </Grid>)
                            })}
                        </Grid>
                    </Grid>
                    <Box pt={4}>
                        <Copyright />
                    </Box>
                </Container>
            </main>
        </div>
    )
}