import React, { useRef } from "react";
import * as d3 from "d3";


export default function BarChartRace(props) {

  const ref = useRef();
  const pdata = props.covid19;
  const pvalue = props.value;
  const tickDuration = 500;
  const top_n = 15;
  const width = window.innerWidth;
  const height = window.innerHeight;
  const margin = { top: 20, right: 0, bottom: 0, left: 0 };
  let barPadding = (height - (margin.bottom + margin.top)) / (top_n * 5);

  d3.selectAll("g").remove();

  const el = d3.select(ref.current)
    .attr("viewBox", [-120, 0, width + 200, height]);

  d3.selectAll("g").remove();

  const svg = el.append("g")
    .attr('width', width)
    .attr('height', height);

  const parsedate = d3.timeFormat("%Y-%m-%d")
  let yesterday = new Date();
  yesterday = parsedate(yesterday.setDate(yesterday.getDate() - 2));
  let period = new Date("2020-02-01");
  let temp = [];

  if (pdata.length > 0) {

    switch (pvalue) {
      case "Confirmed": temp = pdata[0];
        break;
      case "Deaths": temp = pdata[1];
        break;
      case "Recovered": temp = pdata[2];
        break;
      default: temp = pdata[0];
    }

    const data = [...temp.reduce((r, o) => {
      const key = o['Country/Region'] + parsedate(o.Date);
      const item = r.get(key) || Object.assign({}, { country: o['Country/Region'] }, { date: o.Date }, { total: 0 }, { rank: 1 }, { colour: d3.hsl(Math.random() * 360, 0.75, 0.75) });
      item.total += o.Value;
      return r.set(key, item);
    }, new Map()).values()]


    let periodSlice = data.filter(d => (+d.date === +period) && (d.total < 5000000))
      .sort((a, b) => b.total - a.total)
      .slice(0, top_n);

    periodSlice.forEach((d, i) => d.rank = i);

    let x = d3.scaleLinear()
      .domain([0, d3.max(periodSlice, d => d.total)])
      .range([margin.left, width - margin.right]);

    let y = d3.scaleLinear()
      .domain([top_n, 0])
      .range([height - margin.bottom, margin.top]);

    let xAxis = d3.axisTop()
      .scale(x)
      .ticks(width > 500 ? 10 : 5)
      .tickSize(-(height - margin.top - margin.bottom))
      .tickFormat(d => d3.format(',')(d));

    const subTitle = svg.append("text")
      .attr("font-size", 16)
      .attr("x", margin.left + 10)
      .attr("y", margin.top - 5);

    subTitle.text("Countries");

    // const caption = svg.append('text')
    //   .attr("font-size", 16)
    //   .attr('x', width)
    //   .attr('y', height - 5)
    //   .style('text-anchor', 'end');

    // caption.text('*Source: data.humdata.org');

    let periodText = svg.append('text')
      .attr('class', 'periodText')
      .attr('x', width - margin.right)
      .attr('y', height - 25)
      .attr("font-size", 35)
      .style('text-anchor', 'end')
      .text(parsedate(period));

    svg.append('g')
      .attr('transform', `translate(0, ${margin.top})`)
      .style("font", "16px times")
      .call(xAxis)
      .selectAll('.tick line')
      .classed('origin', d => d === 0);

    svg.selectAll('rect.bar')
      .data(periodSlice, d => d.country)
      .enter()
      .append('rect')
      .attr('class', 'bar')
      .attr('x', x(0) + 1)
      .attr('width', d => x(d.total) - x(0))
      .attr('y', d => y(d.rank) + 5)
      .attr('height', y(1) - y(0) - barPadding)
      .style('fill', d => d.colour);

    svg.selectAll('text.label')
      .data(periodSlice, d => d.country)
      .enter()
      .append('text')
      .attr('class', 'label')
      .attr('x', d => x(d.total) - 8)
      .attr('y', d => y(d.rank) + 5 + ((y(1) - y(0)) / 2) + 1)
      .attr("font-size", 16)
      .style('text-anchor', 'end')
      .text(d => d.country);

    svg.selectAll('text.valueLabel')
      .data(periodSlice, d => d.country)
      .enter()
      .append('text')
      .attr('class', 'valueLabel')
      .attr('x', d => x(d.total) + 5)
      .attr('y', d => y(d.rank) + 5 + ((y(1) - y(0)) / 2) + 1)
      .attr("font-size", 16)
      .text(d => d3.format(',.0f')(d.total));


    let ticker = d3.interval(e => {

      periodSlice = data.filter(d => (+d.date === +period) && (d.total < 5000000))
        .sort((a, b) => b.total - a.total)
        .slice(0, top_n);

      periodSlice.forEach((d, i) => d.rank = i);

      x.domain([0, d3.max(periodSlice, d => d.total)]);

      svg.select('.xAxis')
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .call(xAxis);

      let bars = svg.selectAll('.bar').data(periodSlice, d => d.country);
      let labels = svg.selectAll('.label').data(periodSlice, d => d.country);
      let valueLabels = svg.selectAll('.valueLabel').data(periodSlice, d => d.country);


      bars
        .enter()
        .append('rect')
        .attr('class', d => `bar ${d.country.replace(/\s/g, '_')}`)
        .attr('x', x(0) + 1)
        .attr('width', d => x(d.total) - x(0))
        .attr('y', d => y(top_n + 1) + 5)
        .attr('height', y(1) - y(0) - barPadding)
        .style('fill', d => d.colour)
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr('y', d => y(d.rank) + 5);

      bars
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr('width', d => x(d.total) - x(0))
        .attr('y', d => y(d.rank) + 5);

      bars
        .exit()
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr('width', d => x(d.total) - x(0))
        .attr('y', d => y(top_n + 1) + 5)
        .remove();


      labels
        .enter()
        .append('text')
        .attr('class', 'label')
        .attr('x', d => x(d.total) - 8)
        .attr('y', d => y(top_n + 1) + 5 + ((y(1) - y(0)) / 2))
        .style('text-anchor', 'end')
        .text(d => d.country)
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr("font-size", 16)
        .attr('y', d => y(d.rank) + 5 + ((y(1) - y(0)) / 2) + 1);


      labels
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr('x', d => x(d.total) - 8)
        .attr('y', d => y(d.rank) + 5 + ((y(1) - y(0)) / 2) + 1);

      labels
        .exit()
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr('x', d => x(d.total) - 8)
        .attr('y', d => y(top_n + 1) + 5)
        .remove();


      valueLabels
        .enter()
        .append('text')
        .attr('class', 'valueLabel')
        .attr('x', d => x(d.total) + 5)
        .attr('y', d => y(top_n + 1) + 5)
        .text(d => d3.format(',.0f')(d.total))
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr("font-size", 16)
        .attr('y', d => y(d.rank) + 5 + ((y(1) - y(0)) / 2) + 1);

      valueLabels
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr('x', d => x(d.total) + 5)
        .attr('y', d => y(d.rank) + 5 + ((y(1) - y(0)) / 2) + 1)
        .tween("text", function (d) {
          let i = d3.interpolateRound(d.total, d.total);
          return function (t) {
            this.textContent = d3.format(',')(i(t));
          };
        });

      valueLabels
        .exit()
        .transition()
        .duration(tickDuration)
        .ease(d3.easeLinear)
        .attr('x', d => x(d.total) + 5)
        .attr('y', d => y(top_n + 1) + 5)
        .remove();

      periodText.text(parsedate(period));

      if (parsedate(period) === yesterday)
        ticker.stop();
      period.setDate(period.getDate() + 1)
    }, tickDuration);

  }

  return <svg ref={ref} />
}
