import React, { useRef } from 'react';
import * as d3 from 'd3';


export default function MultiLineChart(props) {

    const ref = useRef()
    const pdata = props.covid19;
    const margin = { top: 20, right: 20, bottom: 20, left: 70 };
    const width = window.innerWidth;
    const height = window.innerHeight;

    //const parsedate = d3.timeFormat("%m/%d/%y")
    //const bisectDate = d3.bisector(d => d.date).left;
    //const formatValue = d3.format(',.0f');

    if (pdata.length > 0) {

        const temp = pdata[0];
        const dates = Object.keys(temp[0]).slice(4);
        let data = { dates: dates, series: [] };
        temp.forEach((row) => {
            const name = Object.values(row).slice(1, 2);
            const values = Object.values(row).slice(4);
            data.series.push({ name: name, values: values })
        });

        const x = d3.scaleUtc()
            .domain(d3.extent(data.dates))
            .range([margin.left, width - margin.right]);

            console.log(x.domain())
        const y = d3.scaleLinear()
            .domain([0, d3.max(data.series, d => d3.max(d.values))]).nice()
            .range([height - margin.bottom, margin.top]);

        const xAxis = g => g
            .attr("transform", `translate(0,${height - margin.bottom})`)
            .call(d3.axisBottom(x).ticks(width / 80).tickSizeOuter(0));

        const yAxis = g => g
            .attr("transform", `translate(${margin.left},0)`)
            .call(d3.axisLeft(y))
            .call(g => g.select(".domain").remove())
            .call(g => g.select(".tick:last-of-type text").clone()
                .attr("x", 3)
                .attr("text-anchor", "start")
                .attr("font-weight", "bold")
                .text(data.y));

        const line = d3.line()
            .defined(d => !isNaN(d))
            .x((d, i) => x(data.dates[i]))
            .y(d => y(d));

        // const svg = d3.select(ref.current)
        //     .attr("viewBox", [0, -10, width + margin.left + margin.right + 10, height + margin.top + margin.bottom]);

        const svg = d3.select(ref.current)
            .attr("viewBox", [0, 0, width, height])
            .style("overflow", "visible");

        svg.append("g")
            .call(xAxis);

        svg.append("g")
            .call(yAxis);

        const path = svg.append("g")
            .attr("fill", "none")
            .attr("stroke", "steelblue")
            .attr("stroke-width", 1.5)
            .attr("stroke-linejoin", "round")
            .attr("stroke-linecap", "round")
            .selectAll("path")
            .data(data.series)
            .join("path")
            .style("mix-blend-mode", "multiply")
            .attr("d", d => line(d.values));

        svg.call(hover, path);

        function hover(svg, path) {

            if ("ontouchstart" in document) svg
                .style("-webkit-tap-highlight-color", "transparent")
                .on("touchmove", moved)
                .on("touchstart", entered)
                .on("touchend", left)
            else svg
                .on("mousemove", moved)
                .on("mouseenter", entered)
                .on("mouseleave", left);

            const dot = svg.append("g")
                .attr("display", "none");

            dot.append("circle")
                .attr("r", 2.5);

            dot.append("text")
                .attr("font-family", "sans-serif")
                .attr("font-size", 10)
                .attr("text-anchor", "middle")
                .attr("y", -8);

            function moved() {
                d3.event.preventDefault();
                const mouse = d3.mouse(this);
                const xm = x.invert(mouse[0]);
                const ym = y.invert(mouse[1]);
                const i1 = d3.bisectLeft(data.dates, xm, 1);
                const i0 = i1 - 1;
                const i = xm - data.dates[i0] > data.dates[i1] - xm ? i1 : i0;
                const s = d3.min(data.series, d => Math.abs(d.values[i] - ym));
                path.attr("stroke", d => d === s ? null : "#ddd").filter(d => d === s).raise();
                dot.attr("transform", `translate(${x(data.dates[i])},${y(s.values[i])})`);
                dot.select("text").text(s.name);
            }

            function entered() {
                path.style("mix-blend-mode", null).attr("stroke", "#ddd");
                dot.attr("display", null);
            }

            function left() {
                path.style("mix-blend-mode", "multiply").attr("stroke", null);
                dot.attr("display", "none");
            }
        }
    }

    return <svg ref={ref} />
}
