import React, { useRef } from 'react'
import * as d3 from 'd3'


export default function DonutChart(props) {

    const ref = useRef();
    const pdata = props.covid19;
    const pvalue = props.value;
    let data = [];
    const width = window.innerWidth;
    const height = Math.max(width, window.innerHeight);

    if (pdata.length > 0) {
        const confirmed = pdata[0];
        const deaths = pdata[1];
        const recovered = pdata[2];
        const loc = pdata[3];
        const pconfirmed = pdata[4];
        const pdeaths = pdata[5];
        const precovered = pdata[6];

        data = confirmed.map((item, i) => {
            const foundlocation = loc.find((itmInner) => itmInner['Province/State'] === confirmed[i]['Province/State'] && itmInner['Country/Region'] === confirmed[i]['Country/Region']);
            const founddeaths = deaths.find((itmInner) => itmInner['Province/State'] === confirmed[i]['Province/State'] && itmInner['Country/Region'] === confirmed[i]['Country/Region']);
            const foundrecovered = recovered.find((itmInner) => itmInner['Province/State'] === confirmed[i]['Province/State'] && itmInner['Country/Region'] === confirmed[i]['Country/Region']);
            const recover = foundrecovered ? foundrecovered['Value'] : 0
            return Object.assign({}, item,
                { Region: foundlocation['Region Name'] },
                { Subregion: foundlocation['Sub-region Name'] },
                { Deaths: founddeaths['Value'] },
                { Recovered: recover });
        })

        data = [...data.reduce((r, o) => {
            const key = o.Region;
            // const continent = o.Region === null ? 'Others' : o.Region;
            const item = r.get(key) || Object.assign({}, { continent: o.Region }, { total: 0 });

            switch (pvalue) {
                case "Confirmed": item.total += o.Value / pconfirmed * 100;
                    break;
                case "Deaths": item.total += o.Deaths / pdeaths * 100;
                    break;
                case "Recovered": item.total += o.Recovered / precovered * 100;
                    break;
                default: item.total += o.Value / pconfirmed * 100;
            }

            return r.set(key, item);
        }, new Map()).values()]        

        const svg = d3.select(ref.current)
            .attr("viewBox", [-width / 2, -height / 2, width, height]);

        // const color = d3.scaleOrdinal(d3.schemeCategory10);
        const color = d3.scaleOrdinal()
            .domain(data.map(d => d.continent))
            .range(d3.quantize(t => d3.interpolateSpectral(t * 0.8 + 0.1), data.length).reverse())

        const pie = d3.pie()
            .sort(null)
            .value((d) => d.total);

        const arc = d3.arc()
            .innerRadius(0)
            .outerRadius(Math.min(width, height) / 2)

        const arcs = pie(data);

        svg.selectAll("path")
            .data(arcs)
            .join("path")
            .attr("fill", d => color(d.data.continent))
            .attr("d", arc);

        if (pvalue === "") {

            svg.append("g")
                .attr("text-anchor", "middle")
                .selectAll("text")
                .data(arcs)
                .join("text")
                .attr("transform", d => `translate(${arc.centroid(d)})`)
                .call(text => text.append("tspan")
                    .attr("y", "-0.5em")
                    .attr("font-size", "50px")
                    .text(d => d.data.continent))
                .call(text => text.filter(d => (d.endAngle - d.startAngle) > 0.01).append("tspan")
                    .attr("x", 0)
                    .attr("y", "0.7em")
                    .attr("font-size", "50px")
                    .attr("fill-opacity", 0.7)
                    .text(d => d.data.total.toLocaleString()));
        } else {
            console.log("pie else")
            svg.selectAll("tspan").remove();

            svg.selectAll("text")
                .data(arcs)
                .join("text")
                .attr("transform", d => `translate(${arc.centroid(d)})`)
                .call(text => text.append("tspan")
                    .attr("y", "-0.4em")
                    .attr("font-size", "50px")
                    .text(d => d.data.continent))
                .call(text => text.filter(d => (d.endAngle - d.startAngle) > 0.01).append("tspan")
                    .attr("x", 0)
                    .attr("y", "0.7em")
                    .attr("font-size", "50px")
                    .attr("fill-opacity", 0.7)
                    .text(d => d.data.total.toLocaleString()));
        }
    }

    return <svg ref={ref} />
}
