import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Toolbar from '@material-ui/core/Toolbar';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import * as d3 from 'd3';

import Navigation from './Navigation';
import Copyright from '../Copyright';
import DonutChart from './DonutChart';


const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    title: {
        flexGrow: 1,
    },
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
    },
    container: {
        paddingLeft: theme.spacing(2),
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        paddingRight: theme.spacing(2),
    },
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 400,
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 115,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));


export default function DonutCharts(props) {
    const classes = useStyles();
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

    const [covid19, setCovid19] = useState([]);

    const covid19data = (props) => {
        const covid19confirmed = "time_series_covid19_confirmed_global_narrow.csv"
        const covid19deaths = "time_series_covid19_deaths_global_narrow.csv"
        const covid19recovered = "time_series_covid19_recovered_global_narrow.csv"
        const locurl = "locations.csv"
        const parsedate = d3.timeFormat("%Y-%m-%d")
        const data = Promise.all([
            d3.csv(covid19confirmed, d3.autoType).then(data =>
                data.filter(d => parsedate(d.Date) === props.yesterday)),
            d3.csv(covid19deaths, d3.autoType).then(data =>
                data.filter(d => parsedate(d.Date) === props.yesterday)),
            d3.csv(covid19recovered, d3.autoType).then(data =>
                data.filter(d => parsedate(d.Date) === props.yesterday)),
            d3.csv(locurl, d3.autoType),
        ]).then(result => {
            setCovid19([result[0], result[1], result[2], result[3]]);
        });
        return data
    }

    useEffect(() => {
        const parsedate = d3.timeFormat("%Y-%m-%d");
        let yesterday = new Date();
        yesterday = parsedate(yesterday.setDate(yesterday.getDate() - 1));
        covid19data({ yesterday });
    }, []);


    const valueby = ["Confirmed", "Deaths", "Recovered"];
    const [value, setValue] = useState("");

    const handleChange = (event) => {
        setValue(event.target.value);
    };

    return (
        <div className={classes.root}>
            <Navigation />
            <main className={classes.content}>
                <Toolbar />
                <div className={classes.appBarSpacer} />
                <Container maxWidth="lg" className={classes.container}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={4} lg={6}>
                            <Grid item xs={12} md={8} lg={12}>
                                <FormControl className={classes.formControl}>
                                    <InputLabel id="donut-select-label">Cases</InputLabel>
                                    <Select
                                        labelId="donut-select-label"
                                        id="donut-select"
                                        value={value}
                                        onChange={handleChange}
                                    >
                                        {valueby.map((row) => {
                                            return (<MenuItem key={row} value={row}>{row}</MenuItem>)
                                        })}
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={12} md={8} lg={12}>
                                <Paper className={fixedHeightPaper}>
                                    <DonutChart covid19={covid19} value={value} />
                                </Paper>
                            </Grid>
                        </Grid>
                        <Grid item xs={12} md={4} lg={3}>
                            <Grid item xs={12} md={8} lg={12}>
                                <Paper className={fixedHeightPaper}>
                                </Paper>
                            </Grid>
                        </Grid>
                        <Grid item xs={12} md={4} lg={3}>
                            <Grid item xs={12} md={8} lg={12}>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Box pt={4}>
                        <Copyright />
                    </Box>
                </Container>
            </main>
        </div>
    )
}