import React from 'react';
import clsx from 'clsx';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import NotificationsIcon from '@material-ui/icons/Notifications';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DashboardIcon from '@material-ui/icons/Dashboard';
import PieChartIcon from '@material-ui/icons/PieChart';
import BarChartIcon from '@material-ui/icons/BarChart';
import TableChartIcon from '@material-ui/icons/TableChart';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import FormatLineSpacingIcon from '@material-ui/icons/FormatLineSpacing';
//import MultilineChartIcon from '@material-ui/icons/MultilineChart';

const drawerWidth = 55;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(7),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
}));



export default function Navigation(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="absolute" className={clsx(classes.appBar, classes.appBarShift)}>
        <Toolbar className={classes.toolbar}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            className={clsx(classes.menuButton, classes.menuButtonHidden)}
          >
            <MenuIcon />
          </IconButton>
          <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
            Covid-19 Dashboard
          </Typography>
          <IconButton color="inherit">
            <Badge badgeContent={0} color="secondary">
              <NotificationsIcon />
            </Badge>
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, classes.drawerPaperClose),
        }}
      >
        <div className={classes.toolbarIcon}>
          <IconButton>
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />
        <List>
          <ListItem button>
            <ListItemIcon>
              <Link to='/Dashboard'><DashboardIcon /></Link>
            </ListItemIcon>
            <ListItemText primary="Dashboard" />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <Link to='/PieCharts'><PieChartIcon /></Link>
            </ListItemIcon>
            <ListItemText primary="PieCharts" />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <Link to="/BarCharts"><BarChartIcon /></Link>
            </ListItemIcon>
            <ListItemText primary="BarCharts" />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <Link to='/TableCharts'><TableChartIcon /></Link>
            </ListItemIcon>
            <ListItemText primary="TableCharts" />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <Link to='/LineCharts'><TrendingUpIcon /></Link>
            </ListItemIcon>
            <ListItemText primary="LineCharts" />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <Link to='/RaceCharts'><FormatLineSpacingIcon /></Link>
            </ListItemIcon>
            <ListItemText primary="RaceCharts" />
          </ListItem>
          {/* <ListItem button>
            <ListItemIcon>
              <Link to='/MultiLineCharts'><MultilineChartIcon /></Link>
            </ListItemIcon>
            <ListItemText primary="MultiLineCharts" />
          </ListItem>           */}
          {/* <ListItem button>
            <ListItemIcon>
              <Link to='/Temps'><FormatLineSpacingIcon /></Link>
            </ListItemIcon>
            <ListItemText primary="Temps" />
          </ListItem>           */}
        </List>
      </Drawer>
    </div >
  )
}